<template>
  <div>
    <skeleton v-if="isSkeletonLoading" :nums="2" fheight="4rem" iheight="2rem"></skeleton>
    <div v-else class="partners">
      <div class="partners-background"></div>
      <van-nav-bar left-arrow @click-left="back" class="partners-header" />
      <div class="partners-content">
        <div class="partners-slogan">
          邀请您成为{{$util.isSyBird()?'三翼鸟':'智家'}}平台合伙人，一起开启海尔新零售！
        </div>
        <div class="partners-submit">
          <van-field v-model="telPhone" placeholder="请输入手机号" type="tel" :maxlength="13" @keyup="telPhoneBlur" :disabled="disable">
            <template #right-icon>
              <van-icon v-if="telPhone" name="clear" @click.stop="telPhone= ''" />
            </template>
          </van-field>
          <van-field v-if="imgCodeShow" v-model="imgCode" placeholder="请输入图形验证码" :maxlength="4" :disabled="disable">
            <template #button>
              <img class="partners-submit_imgCode" :src="src" @click="getPhoto" />
            </template>
          </van-field>
          <van-field v-model="yzCode" :maxlength="6" type="number" placeholder="请输入验证码" :disabled="disable">
            <template #button>
              <div :class="{'partners-submit_code':true,'partners-submit_code--disable':!allow}" @click="getMessage">{{codeText}}</div>
            </template>
          </van-field>
          <div class="partners-agreement">
            <van-checkbox v-model="isChecked" style="display:inline-block;margin-bottom:-.1rem;"></van-checkbox>
            若为新用户提交即代表同意
            <a href="https://m.ehaier.com/v3/mstore/sg/helpDetail.html?id=1002">《海尔智家商城用户服务协议》</a>、
            <a href="https://m.ehaier.com/v3/mstore/sg/helpDetail.html?id=1206">《海尔智家商城用户隐私权政策》</a>、
            <a href="http://account.haier.com/html/privacypolicy.html">《海尔家电隐私权政策》</a>
          </div>
          <van-button round type="info" :disabled="!isChecked" @click="submit" :loading="isLoading">提交</van-button>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import Vue from 'vue'
import { Field, Checkbox, Button } from 'vant';
import personalApi from '@/http/service/login.js';
// import personalApiIndex from '@/http/service/index';
import { myManageinfo } from '@/http/service/mine'
import { bindWxAccount } from '@/http/service/partners'
import skeleton from '@/components/skeleton/index.vue'
import UplusApi from '@uplus/uplus-api';
import semver from '@/utils/semver'
import utils from '@/utils/utils'
import URL from '@/http/url.js'
const instance = new UplusApi()
Vue.use(Field)
Vue.use(Checkbox)
Vue.use(Button)
export default {
  name: 'partners',
  components: { skeleton },
  data () {
    return {
      isSkeletonLoading: true,
      storeName: '这是商店名称',
      mobileAccount: '',
      messageMM: '',
      codeText: '获取验证码',
      yzCode: '',
      telPhone: '',
      // 是否同意协议
      isChecked: false,
      allow: true,
      imgCodeShow: false,
      imgCode: '',
      src: '',
      isLoading: false,
      disable: false,
      unionid: '',
    }
  },
  created () {
    this.$util.smMsgSdk()
    // 反作弊逻辑
    const _query = this.$route.query;
    const that = this;
    if (_query.share && _query.activityId) {
      if (utils.isWeixin()) {
        if (_query.code) {
          that.isSkeletonLoading = false;
          // 获取openId
          that.$http.get(URL.getWechatUserInfo, { params: { code: _query.code } }).then(({ data }) => {
            if (data.success) {
              if (data.data.unionid) {
                that.unionid = data.data.unionid
                if (that.$store.state.isLogin) {
                  that.checkInfo()
                  that.isSkeletonLoading = false;
                } else {
                  that.isSkeletonLoading = false;
                }
              } else {
                that.$toast({ message: '获取UnionId失败，请刷新页面', duration: 0 })
              }
            } else {
              // 授权失败
              that.$toast({ message: data.message, duration: 0 })
            }
            console.log(data)
          })
        } else {
          // 获取授权
          that._doGetWxCode();
          this.isSkeletonLoading = false;
        }
      } else {
        this.$toast({ message: '请在微信环境打开邀请链接', duration: 0 })
      }
    } else {
      if (this.$store.state.isLogin) {
        this.checkInfo()
      } else {
        this.isSkeletonLoading = false;
      }
    }
  },
  methods: {
    // 退出登录
    _logout() {
      this.$store.dispatch('logout')
      // const data = {};
      // personalApiIndex.logout(data).then(res => {
      //   const _resData = res.data;
      //   if (_resData.success) {
      //     // 清除localStorage缓存
      //     localStorage.clear();
      //     // 清除store的数据
      //     this.$store.dispatch('resetStore')
      //     gio && gio('clearUserId');
      //     // 跳转到登陆页面
      //   } else {
      //     this.$toast.fail(_resData.message);
      //   }
      // })
    },
    bindWxAccount() {
      bindWxAccount({ unionId: this.unionid, bindType: 7 }).then((result) => {
        const { message, success } = result.data
        if (success) {
          this.success();
        } else {
          this.$toast({ message: message,
            duration: 3000,
            onClose: () => {
              this._logout();
            } })
        }
      }).catch((err) => {
        this.$toast({ message: err.message,
          duration: 3000,
          onClose: () => {
            this._logout();
          } })
      });
    },
    telFormatter (v, b) {
      return v.replace(/(\d{3})(\d{4})/, '$1 $2 ')
    },
    // 获取验证码
    getMessage () {
      if (!this.allow) {
        return
      }
      let phone = this.telPhone.replace(/\s/g, '')
      if (phone.length !== 11 || !(/^1[3456789]\d{9}$/.test(phone))) {
        this.$toast(
          { message: '请输入正确的手机号吗!' })
        return
      }
      this.$util.dealSmDeviceId().then(deviceId => {
        personalApi.register({ mobile: phone, imgCaptcha: this.imgCode }, deviceId).then(res => {
          if (res.data.success === false) {
            this.$toast(res.data.message)
          } else {
            this.allow = false
            this.$toast('短信验证码发送成功，请及时验证！')
            // 倒计时
            let time = 60;
            this.timer = setInterval(() => {
              if (time === 0) {
                this.allow = true
                clearInterval(this.timer);
                this.codeText = '重发验证码';
              } else {
                this.codeText = '重新发送(' + time + 's)';
                this.allow = false;
                time--
              }
            }, 1000)
          }
        })
      })
    },
    submit () {
      let phone = this.telPhone.replace(/\s/g, '')
      if (phone.length !== 11 || !(/^1[3456789]\d{9}$/.test(phone))) {
        this.$toast(
          { message: '请输入正确的手机号吗!' })
        return
      }
      if (!this.yzCode) {
        this.$toast(
          { message: '请输入验证码!' })
        return
      }
      this.isLoading = true
      // $util.isSyBird()
      let param = {
        type: 'mobile',
        mobile: phone,
        regSource: 'PtpartnerReg',
        captcha: this.yzCode
      }
      if (this.$util.isSyBird()) {
        param.regSource = 'SynApp'
      }
      this.$store.dispatch('doH5Login', param).then(res => {
        if (res.mid) {
          this.checkInfo()
        } else {
          this.$toast({
            message: res.message
          })
        }
      }).finally(() => {
        this.isSkeletonLoading = false;
        this.isLoading = false
      })
    },
    getPhoto () {
      this.src = this.$HOST + '/v3/platform/web/member/register/getImgCaptcha.html?rnd=' + Math.random() + '&flag=' + localStorage.getItem('sessionValue')
    },
    telPhoneBlur () {
      let phone = this.telPhone.replace(/\s/g, '')
      if (phone.length === 11 && (/^1[3456789]\d{9}$/.test(phone))) {
        this.getPhoto()
        this.imgCodeShow = true
      } else if (phone.length === 11 && !(/^1[3456789]\d{9}$/.test(phone))) {
        this.$toast(
          { message: '请输入正确的手机号吗!' })
      }
    },
    back () {
      this.$store.dispatch('doHistoryBack')
    },
    // 成功回调
    success () {
      // 校验身份成功后 再校验微信绑定
      let query = this.$route.query
      this.$router.replace({
        path: '/applyPartner',
        query: {
          activityId: query.activityId || '',
          inviteStoreId: query.share || '',
          sg_rn_app: query.sg_rn_app || '',
        }
      })
    },
    // 校验身份
    checkInfo () {
      setTimeout(() => {
        myManageinfo(null, true).then((res) => {
          let data = res.data.data
          let toast = '';
          if (!data) {
            this.isSkeletonLoading = false;
            return
          }
          if (data.identifyFlag === 0) { // normalMicroUser: 1-普通用户 2-储备微店主
            const _query = this.$route.query;
            // this.success()
            if (data.bindWx === 0 && _query.share && _query.activityId) {
              // 获取授权
              if (this.unionid) { // 已授权 未绑定
                this.bindWxAccount()
              } else { // 未授权
                this._doGetWxCode();
              }
            } else {
            // 已绑定微信 或者不需要绑定
              this.success()
            }
          } else {
            if ([21, 22].includes(data.identifyFlag)) {
              toast = '您已是店铺合伙人'
            } else if (data.identifyFlag === 31) {
              toast = '您已是平台合伙人'
            } else if (Number(data.identifyFlag) >= 10 && Number(data.identifyFlag) <= 20) {
              toast = '您已是商家身份'
            } else {
              toast = '您暂无申请权限'
            }
            this.disable = true
            this.$toast(toast);
            setTimeout(() => {
              this._toHome();
            }, 1500);
          }
        }).catch(e => {
          this.isSkeletonLoading = false;
        })
      }, 600);
    },
    _toHome() {
      if (this.$util.isHaierApp()) {
        instance.initDeviceReady().then(() => {
          instance.upAppinfoModule.getAppInfo().then(result => {
            let appVersion = result.retData.appVersion
            if (appVersion && semver.gte(appVersion, '6.20.0')) {
              this.$util.openNewWindow('https://uplus.haier.com/uplusapp/customize/index.html?close_current_page=true', true, true)
              //
            } else {
              instance.initDeviceReady().then(function() {
                instance.closeH5ContainerView();
              });
            }
          })
        })
      } else {
        this.$router.replace({ path: '/' })
      }
    },
    // 微信授权
    _doGetWxCode() {
      let redirectUrl = location.href
      utils.getWxUserInfoCode('wxb03a523baf487993', redirectUrl);
    }
  },
  watch: {
    telPhone (newValue, oldValue) {
      this.telPhone = newValue.length > oldValue.length ? newValue.replace(/[^0-9]/g, '').replace(/(\d{3})(\d{0,4})(\d{0,4})/, '$1 $2 $3') : this.telPhone.trim()
    }
  }
}
</script>
