    color: #ffffff;
    line-height: 0.56rem;
    padding:0 .48rem;
  }
  &-submit {
    margin-top: 0.44rem;
    height: 5.7rem;
    opacity: 1;
    background: #ffffff;
    border-radius: 0.16rem;
    padding: 0.48rem;
    &_name {
      font-size: 0.34rem;
      font-weight: 500;
      text-align: center;
      color: #333333;
